import React, {useEffect} from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {getStatus, getUserProfile, savePhoto, saveProfile, updateStatus} from "../../redux/profileReducer";
import {compose} from "redux";



let ProfileContainer = (props) => {

    useEffect(() => {
       props.getUserProfile(props.userId);
       props.getStatus(props.userId)
    }, [props.userId]);


        return (<div>
            <Profile {...props} profile={props.profile} status={props.status}
                     updateStatus={props.updateStatus} userId={props.userId} isOwner={props.isOwner}/>
        </div>)
}

let mapStateToProps = (state) => {
    return {
        profile: state.profilePage.profile,
        isAuth: state.auth.isAuth,
        status: state.profilePage.status,
        authorisedId: state.auth.userId
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        getUserProfile: (userId) => {
            dispatch(getUserProfile(userId))
        },
        getStatus: (userId) => {
            dispatch(getStatus(userId))
        },
        updateStatus: (status) => {
            dispatch(updateStatus(status))
        },
        savePhoto: (file) => {
            dispatch(savePhoto(file))
        },
        saveProfile: (profile) => {
            dispatch(saveProfile(profile))
        }
    }
}

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    //withAuthRedirect
)(ProfileContainer)
