import classes from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";
import ProfileStatusHooks from "./profileStatusHooks";
import userPhoto from "../../../assets/images/user.png";
import {useState} from "react";
import {ErrorMessage, Field, Form, Formik} from "formik";
import {AddMessageSchema, LoginSchema} from "../../FormValidators/ValidationFormSchema";

const ProfileInfo = (props) => {
    let [editMode, setEditMode] = useState(false)

    if (!props.profile) {
        return <Preloader/>
    }

    let onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }

    return (<div>
        <div>
            <img className={classes.wallpaper} src="https://www.imageen.com/graphics/info/sunset_ocean_tree.jpg"/>
        </div>
        <div className={classes.description}>
            <img className={classes.avatar}
                 src={props.profile.photos.large || userPhoto}/>
            <div>
                {props.isOwner && <input type={"file"} onChange={onMainPhotoSelected}/>}
            </div>
            <div>
                <b>Status:</b> <ProfileStatusHooks status={props.status} updateStatus={props.updateStatus}
                                                   userId={props.userId}/>
            </div>
            <div>
                {editMode ?
                    <ProfileDataForm profile={props.profile} saveProfile={props.saveProfile} goToEditMode={() => {
                        setEditMode(false)
                    }}/> :
                    <ProfileData profile={props.profile} isOwner={props.isOwner} goToEditMode={() => {
                        setEditMode(true)
                    }}/>}
            </div>
        </div>
    </div>)
}

const ProfileData = (props) => {
    return <div>
        {props.isOwner && <div>
            <button onClick={props.goToEditMode}>edit</button>
        </div>}
        <div><b>Full Name:</b> {props.profile.fullName}</div>
        <div><b>About Me:</b> {props.profile.aboutMe}</div>
        <div>lookingForAJob:
            {props.profile.lookingForAJob === true ? " yes" : " no"}
            <div><b>My profecional skills</b>
                {props.profile.lookingForAJobDescription}</div>
        </div>
    </div>
}


const ProfileDataForm = (props) => {
    return (<Formik
        initialValues={{fullName: '', aboutMe: ''}}

        onSubmit={(values, {setSubmitting}) => {
            debugger;
            props.saveProfile(values)
            props.goToEditMode();
            setSubmitting(false);
        }}
    >
        {({isSubmitting}) => (<Form>
            <div><b>Full Name:</b>
                <Field type="textarea" name="fullName" placeholder="Full Name"/>
                <ErrorMessage name="fullName" component="div" className={classes.error}/>
            </div>
            <div><b>About Me:</b>
                <Field type="textarea" name="aboutMe" placeholder="About Me"/>
                <ErrorMessage name="aboutMe" component="div" className={classes.error}/>
            </div>
            <div>lookingForAJob:
                <Field type="checkbox" name="lookingForAJob"/>
            </div>
            <div><b>My profecional skills</b>:
                <Field type="textarea" name="lookingForAJobDescription"/>
            </div>
            <button type="submit" disabled={isSubmitting}>
                Save
            </button>
        </Form>)}
    </Formik>)
}

export default ProfileInfo;